import React, { FC } from "react";


import FlexContainer from "../containers/FlexContainer";
import Star from "../Star/Star";
import { Tooltip, Rating, DeliveryTime } from "./DeliveryTooltip.styles";

interface DeliveryTooltipProps {
    rating: number;
    deliveryTime: number;
    visible: boolean;
}


const DeliveryTooltip: FC<DeliveryTooltipProps> = ({deliveryTime, rating, visible}) => {
    return (
        <Tooltip className={visible ? 'visible' : ''}>
            <FlexContainer justify='space-between'>
                <DeliveryTime>
                    <span className='time'>{deliveryTime} </span>
                    <span className='approximately-time'>  </span>
                    <span className='note'>    60 ,      </span>
                </DeliveryTime>
                <Rating>
                    <span className='rate'>
                        {rating}
                        {Array(rating).fill(0).map((_, index) => <Star key={index}/>)}
                    </span>
                    <span className='marks'>3017 </span>
                    <span className='note'>     </span>
                </Rating>
            </FlexContainer>
            <span className='info'>   7    </span>
        </Tooltip>
    )
}

export default DeliveryTooltip;